.mainLogo {
  max-width: 80%;
  max-height: 80%;
}

@media (min-width: 768px) {
  .mainLogo {
    max-width: 40%;
  }
}

a {
  text-decoration: none;
  color: black;

}


.background {
  background-image: url("./imgs/Background.png");
  background-size: cover;
  background-repeat: no-repeat;
  // -webkit-animation: kenburns-top 10s ease-out both;
  //         animation: kenburns-top 10s ease-out both;
}

@-webkit-keyframes kenburns-top {
  0% {
     -webkit-transform: scale(1) translateY(0);
             transform: scale(1) translateY(0);
  }
  100% {
     -webkit-transform: scale(1.5) translateY(-15px);
             transform: scale(1.5) translateY(-15px);
  }
}

@keyframes kenburns-top {
  0% {
     transform: scale(1) translateY(0);
  }
  100% {
     transform: scale(1.5) translateY(-15px);
  }
}


.appBar {
  min-height: 3em;
  background-color: black;
}

.blur-background {
  backdrop-filter: blur(20px);
  background-color: rgba(0, 0, 0, 0.5) !important;
  border-radius: 4 !important;
  border: 2px !important;
  border-color: black !important;
  overflow: hidden !important;
}

.shadow {
  filter: drop-shadow(5px 0 0 rgb(0, 0, 0)) drop-shadow(0 5px 0 rgb(0, 0, 0)) drop-shadow(-5px 0 0 rgb(0, 0, 0)) drop-shadow(0 -5px 0 rgb(0, 0, 0));
  -webkit-filter: drop-shadow(5px 0 0 rgb(0, 0, 0)) drop-shadow(0 5px 0 rgb(0, 0, 0)) drop-shadow(-5px 0 0 rgb(0, 0, 0)) drop-shadow(0 -5px 0 rgb(0, 0, 0));
}



.fade-in {
  -webkit-animation: fade-in 0.5s linear forwards;
  animation: fade-in 0.5s linear forwards
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0
  }

  100% {
    opacity: 1
  }
}

@keyframes fade-in {
  0% {
    opacity: 0
  }

  100% {
    opacity: 1
  }
}

.puff-in-hor {
  -webkit-animation: puff-in-hor .3s cubic-bezier(.47, 0.000, .745, .715) both;
  animation: puff-in-hor .3s cubic-bezier(.47, 0.000, .745, .715) both
}

@-webkit-keyframes puff-in-hor {
  0% {
    -webkit-transform: scaleX(2);
    transform: scaleX(2);
    opacity: 0
  }

  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 1
  }
}

@keyframes puff-in-hor {
  0% {
    -webkit-transform: scaleX(2);
    transform: scaleX(2);
    opacity: 0
  }

  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 1
  }
}

.slide-in-blurred-right {
  -webkit-animation: slide-in-blurred-right .6s cubic-bezier(.23, 1.000, .32, 1.000) both;
  animation: slide-in-blurred-right .6s cubic-bezier(.23, 1.000, .32, 1.000) both
}

@-webkit-keyframes slide-in-blurred-right {
  0% {
    -webkit-transform: translateX(1000px) scaleX(2.5) scaleY(.2);
    transform: translateX(1000px) scaleX(2.5) scaleY(.2);
    -webkit-transform-origin: 0 50%;
    transform-origin: 0 50%;
    -webkit-filter: blur(40px);
    filter: blur(40px);
    opacity: 0
  }

  100% {
    -webkit-transform: translateX(0) scaleY(1) scaleX(1);
    transform: translateX(0) scaleY(1) scaleX(1);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1
  }
}

@keyframes slide-in-blurred-right {
  0% {
    -webkit-transform: translateX(1000px) scaleX(2.5) scaleY(.2);
    transform: translateX(1000px) scaleX(2.5) scaleY(.2);
    -webkit-transform-origin: 0 50%;
    transform-origin: 0 50%;
    -webkit-filter: blur(40px);
    filter: blur(40px);
    opacity: 0
  }

  100% {
    -webkit-transform: translateX(0) scaleY(1) scaleX(1);
    transform: translateX(0) scaleY(1) scaleX(1);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1
  }
}